@use '../util/' as *;

.container {
  max-width: rem(1110);
  margin: 0 auto rem(46);
  padding: 0 rem(25);

  @include breakpoint(large) {
    padding: 0;
  }
}

.cards-grid {
  display: grid;
  grid-template-columns: 1fr;
  gap: rem(30);

  @include breakpoint(medium) {
    grid-template-columns: repeat(2, 1fr);
  }

  @include breakpoint(large) {
    grid-template-columns: repeat(4, 1fr);
  }

  &--2nd {
    row-gap: rem(23);
  }
}
