*::after,
*::before {
  margin: 0;
  padding: 0;
  box-sizing: inherit;
}

html {
  box-sizing: border-box;
  font-size: 100%;
}

body {
  background-color: var(--dark-bg);
}

.visually-hidden {
  position: absolute;
  top: auto;
  left: -10000px;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

:root {
  --font-inter: "Inter", sans-serif;
}

body {
  font-family: var(--font-inter);
  font-weight: 400;
  color: var(--dark-text-1);
}

h1,
h2,
h3 {
  margin-top: 0;
  line-height: 1.1;
  color: var(--dark-text-2);
}

h1 {
  font-size: 1.5rem;
  margin-bottom: 3px;
}
@media (min-width: 71.875em) {
  h1 {
    font-size: 1.75rem;
  }
}

h2 {
  font-size: 1.5rem;
  margin-bottom: 1.5rem;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

:root {
  --limegreen: hsl(163, 72%, 41%);
  --brightred: hsl(356, 69%, 56%);
  --facebook: hsl(208, 92%, 53%);
  --twitter: hsl(203, 89%, 53%);
  --instagram-start: hsl(37, 97%, 70%);
  --instagram-end: hsl(329, 70%, 58%);
  --youtube: hsl(348, 97%, 39%);
  --dark-toggle-start: hsl(210, 78%, 56%);
  --dark-toggle-end: hsl(146, 68%, 55%);
  --light-toggle: hsl(230, 22%, 74%);
  --dark-bg: hsl(230, 17%, 14%);
  --dark-top-bg: hsl(232, 19%, 15%);
  --dark-card-bg: hsl(228, 28%, 20%);
  --dark-card-bg-hover: hsl(228, 25%, 27%);
  --dark-text-1: hsl(228, 34%, 66%);
  --dark-text-2: hsl(0, 0%, 100%);
  --light-bg: hsl(0, 0%, 100%);
  --light-top-bg: hsl(225, 100%, 98%);
  --light-card-bg: hsl(227, 47%, 96%);
  --light-text-1: hsl(230, 17%, 14%);
  --light-text-2: hsl(228, 12%, 44%);
}

.container {
  max-width: 69.375rem;
  margin: 0 auto 2.875rem;
  padding: 0 1.5625rem;
}
@media (min-width: 71.875em) {
  .container {
    padding: 0;
  }
}

.cards-grid {
  display: grid;
  grid-template-columns: 1fr;
  gap: 1.875rem;
}
@media (min-width: 40em) {
  .cards-grid {
    grid-template-columns: repeat(2, 1fr);
  }
}
@media (min-width: 71.875em) {
  .cards-grid {
    grid-template-columns: repeat(4, 1fr);
  }
}
.cards-grid--2nd {
  row-gap: 1.4375rem;
}

.header {
  display: flex;
  flex-wrap: wrap;
  margin-top: 2.25rem;
}
@media (min-width: 40em) {
  .header {
    justify-content: space-between;
    align-items: center;
  }
}
.header__title {
  width: 100%;
  margin-bottom: 0.5rem;
}
@media (min-width: 40em) {
  .header__title {
    width: auto;
    margin-bottom: 0;
  }
}
.header__subtitle {
  font-size: 0.875rem;
  font-weight: 700;
}

.toggle {
  display: grid;
  grid-template-columns: 1fr 3rem;
  column-gap: 0.5rem;
  border: none;
  margin: 0;
  padding: 0;
}
.toggle label {
  align-self: center;
  font-weight: 700;
  font-size: 0.875rem;
  color: var(--dark-text-2);
}
.toggle label[for=dark] {
  grid-column: 1/2;
}
.toggle__wrapper {
  grid-column: 2/3;
  position: relative;
  height: 1.5rem;
}
.toggle input[type=radio] {
  display: inline-block;
  position: relative;
  z-index: 1;
  margin: 0 -0.125rem;
  width: 1.25rem;
  height: 1.5rem;
  opacity: 0;
  cursor: pointer;
}
.toggle input[type=radio]:focus ~ .toggle__switcher {
  top: 0.125rem;
  border: 2px solid var(--dark-text-2);
  width: calc(1.125rem - 2px);
  height: calc(1.125rem - 2px);
}
.toggle__background {
  display: block;
  position: absolute;
  z-index: 0;
  top: 0;
  width: 100%;
  height: 100%;
  border-radius: 0.75rem;
  background: linear-gradient(225deg, var(--dark-toggle-end) 0%, var(--dark-toggle-start) 98.02%);
}
.toggle__switcher {
  display: block;
  position: absolute;
  z-index: 2;
  top: 0.1875rem;
  left: 0.1875rem;
  width: 1.125rem;
  height: 1.125rem;
  border-radius: 50%;
  background-color: var(--dark-bg);
  transition: all 0.1s ease-in-out;
}
.toggle #system:checked ~ .toggle__switcher {
  left: 50%;
  right: auto;
  transform: translate(-50%);
}
.toggle #light:checked ~ .toggle__switcher {
  left: calc(100% - 1.125rem - 0.1875rem);
}

.card {
  border-radius: 0.3125rem;
  background-color: var(--dark-card-bg);
  padding: 1.5625rem;
  text-align: center;
  transition: background 150ms ease-in-out;
  cursor: pointer;
}
.card:hover {
  background-color: var(--dark-card-bg-hover);
}
.card--facebook {
  border-top: 0.25rem solid var(--facebook);
}
.card--twitter {
  border-top: 0.25rem solid var(--twitter);
}
.card--instagram {
  position: relative;
  border-top: 0.25rem solid transparent;
  background-clip: padding-box;
}
.card--instagram:before {
  content: "";
  position: absolute;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: -1;
  margin-top: -0.25rem;
  border-radius: inherit;
  background: linear-gradient(to right, var(--instagram-start), var(--instagram-end));
}
.card--youtube {
  border-top: 0.25rem solid var(--youtube);
}
.card__platform {
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: 0.3125rem;
  margin-bottom: 1.75rem;
  height: 1.25rem;
}
.card__icon {
  margin-right: 0.5rem;
}
.card__username {
  font-weight: 700;
  font-size: 0.75rem;
}
.card__count {
  font-weight: 700;
  letter-spacing: -0.125rem;
  line-height: 1;
  color: var(--dark-text-2);
}
.card__count--big {
  font-size: 3.5rem;
  margin-bottom: 0.25rem;
}
.card__count--small {
  font-size: 2rem;
}
.card__label {
  margin-bottom: 1.5625rem;
  text-transform: uppercase;
  font-size: 0.75rem;
  letter-spacing: 0.3125rem;
}
.card__change {
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: 700;
  font-size: 0.75rem;
}
.card__change--up {
  color: var(--limegreen);
}
.card__change--down {
  color: var(--brightred);
}
.card__change img {
  margin-right: 0.25rem;
}

.card-small {
  display: grid;
  grid-template-columns: repeat(2, 1fr);
  grid-template-rows: repeat(2, auto);
  justify-items: start;
  row-gap: 1.4375rem;
}
.card-small .card__subtitle {
  font-weight: 700;
  font-size: 0.75rem;
}
.card-small .card__count,
.card-small .card__change {
  align-self: end;
}
.card-small img,
.card-small .card__change {
  justify-self: end;
}